html {
  box-sizing: border-box;
}
html *,
::after,
::before {
  box-sizing: inherit;
}
body{
  height: 100vh;
}
h1,
h2,
h3,
h4,
h5,
p,
ul,
ol,
li {
  margin: 0;
}

ul,
ol {
  list-style: none;
}

img {
  display: block;
}

ul {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Gilroy', 'Sans-Serif';
}

button,
select {
  cursor: pointer;
  background: none;
  padding: 0;
  margin: 0;
  border: 0;
}
a {
  text-decoration: none;
  // color: rgba(25, 37, 153, 1);

}

input {
  padding: 0;
  margin: 0;
}
